body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  background-image: url(/react-to-do-app/static/media/bgc.5d3f1c2a.jpg);
  background-size: contain;
  background-position: center;
  /* background-repeat: no-repeat; */
}

.AddTask {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
}

.Form {
    margin: 50px 0;
}

.AddTask label, .AddTask input {
    font-size: 18px;
    padding: 10px;
    margin-left: 10px;
}
.AddTask input {
    border: 1px solid #000;
    border-radius: 5px;
}
.AddTask input[type='checkbox'] {
    width: 20px;
    height: 20px;
    margin-right: 10px;
}
.AddTask label {
    color: #fff;
    font-size: 20px;
}
.AddTask button {
    font-size: 18px;
    margin-left: 20px;
    padding: 10px 35px;
    border-radius: 5px;
    background-color: #fff;
    border: 1px solid black;
    -webkit-transition: .5s;
    transition: .5s;
}
.AddTask button:hover {
    color: #fff;
    background-color: transparent;
    border: 1px solid #fff;
}

.ActiveTask,
.DoneTask {
   display: -webkit-flex;
   display: flex;
   -webkit-flex-direction: column;
           flex-direction: column;
   background-color: rgba(55, 61, 51, .5);
   margin: 10px;
   padding: 20px 0;
}
.ActiveTask:nth-of-type(even),
.DoneTask:nth-of-type(even) {
   background-color: rgba(132, 134, 130, .5);
}

.ActiveTask div:first-child,
.DoneTask div:first-child {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}
.ActiveTask div:first-child span:first-of-type,
.DoneTask div span:first-of-type {
    font-size: 22px;
    color: #fff;
    letter-spacing: 2px;
}
.ActiveTask div:first-child span:last-of-type,
.DoneTask div span:last-of-type,
.DoneTask div em {
    font-size: 18px;
    color: #fff;
    padding: 10px 0;
}

.ActiveTaskButtonsWrapper {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
}

.ActiveTaskButtonsWrapper button,
.DoneTask button {
    color: #fff;
    background-color: transparent;
    font-size: 14px;
    padding: 8px 15px;
    border: 1px solid #000;
    border-radius: 5px;
    cursor: pointer;
    -webkit-transition: .5s;
    transition: .5s;
    margin-top: 20px;
    letter-spacing: 2px;
}

.DoneTask button {
    -webkit-align-self: center;
            align-self: center;
}
.ActiveTaskButtonsWrapper button:last-child {
    margin-left: 40px;  
}

.ActiveTask:nth-of-type(odd) .ActiveTaskButtonsWrapper button:hover {
    color: rgb(84, 94, 77);
    background-color: #fff;
    font-weight: bold;
}
.ActiveTask:nth-of-type(even) .ActiveTaskButtonsWrapper button:hover {
    color: rgb(132, 134, 130);
    background-color: #fff;
    font-weight: bold;
}
.TaskList {
    margin: 0 auto;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
}

.ToDo, .Done {
    -webkit-flex-basis: 35%;
            flex-basis: 35%;
    text-transform: capitalize;
}
.ToDo h1, .ToDo h3, .Done h1, .Done h3{
    color: #fff;
    font-weight: normal;
    letter-spacing: 4px;
    margin: 0;
    background-color:   rgba(44, 49, 41, .9);
}

.ToDo h1, .Done h1 {
    padding-top: 20px;
}

.ToDo h3, .Done h3 {
    padding-bottom: 20px;
}

.Done span.Warning {
    font-size: 17px;
    color: #fff;
}

.WarningWrapper {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    background-color: rgb(187, 36, 36);
    padding: 10px 0;
}

.WarningWrapper button {
    -webkit-align-self: center;
            align-self: center;
    color: #fff;
    background-color: transparent;
    font-size: 14px;
    padding: 8px 15px;
    border: 1px solid #000;
    border-radius: 5px;
    cursor: pointer;
    -webkit-transition: .5s;
    transition: .5s;
    margin-top: 5px;
    letter-spacing: 2px;
    transition: .5s;
}

.WarningWrapper button:hover {
    background-color: #fff;
    color: rgb(187, 36, 36);
    font-weight: bold;
}

